import mpvuePicker from '../template/mpvue-picker/mpvuePicker.vue';
var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
export default {
	data() {
		return {
			mode:'',
			pickerAry:[],
			pickerDefault:[],
			deep:0,
			imageList:[]
		}
	},
	components:{
		mpvuePicker
	},
	methods: {
		pickershow(){
			this.mode = 'multiLinkageSelector';
			this.pickerAry = [
				{
					label:'第一',
					value:1,
					children:
					[
						{
							label:'1-1',
							value:1
						},
						{
							label:'1-2',
							value:2
						}
					]
				},
				{
					label:'第二',
					value:2,
					children:
					[
						{
							label:'2-1',
							value:1
						},
						{
							label:'2-2',
							value:2
						},
						{
							label:'2-3',
							value:3
						}
					]
				},
				{
					label:'第三',
					value:3,
					children:
					[
						{
							label:'3-1',
							value:1
						},
						{
							label:'3-2',
							value:2
						}
					]
				}];
			this.pickerDefault = [0];
			this.deep = 2;
			this.$refs.mpvuePicker.show();
		},
		chooseImage: async function() {
			if (this.imageList.length === 4) {
				uni.showModal({
					content:'图片数量已达上限'
				})
				return;
			}
			uni.chooseImage({
				sourceType: sourceType[2],
				sizeType: sizeType[2],
				count: 4 - this.imageList.length,
				success: (res) => {
					this.imageList = this.imageList.concat(res.tempFilePaths);
				}
			})
		},
		previewImage: function(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		deleteImg: function(e){
			var current = e.target.dataset.src
			this.imageList.splice(this.imageList.findIndex(item => item === current), 1)
		},
			
		submit(){
			console.log(this.$fly)
			this.$fly.get("/abc")
			.then(function(response){
				console.log(response);
			}).catch(function(error){
				console.log(error);
			})
			
			uni.request({
				method:'GET',
				url:'https://www.baidu.com',
				success:function(e){
					console.log(e);
				}
			})
		}
	}
}
